'use server';

import { z } from 'zod';
import { rateLimitEmail, type RateLimitEmailOutput } from '@/ai/flows/ai-rate-limiter';
import type { EmailBatch, EmailResult } from '@/lib/types';

const FormSchema = z.object({
  subject: z.string().min(1, 'Subject is required.'),
  body: z.string().min(1, 'Email body is required.'),
  recipients: z.string().min(1, 'Recipients are required.'),
});

type State = {
  message?: string | null;
  errors?: {
    subject?: string[];
    body?: string[];
    recipients?: string[];
  };
  batch?: EmailBatch | null;
};

export async function sendBulkEmail(prevState: State, formData: FormData): Promise<State> {
  const validatedFields = FormSchema.safeParse({
    subject: formData.get('subject'),
    body: formData.get('body'),
    recipients: formData.get('recipients'),
  });

  if (!validatedFields.success) {
    return {
      errors: validatedFields.error.flatten().fieldErrors,
      message: 'Missing Fields. Failed to Send Email.',
    };
  }

  const recipients = validatedFields.data.recipients
    .split(/[\n,;]+/)
    .map((email) => email.trim())
    .filter((email) => email.length > 0 && z.string().email().safeParse(email).success)
    .filter((value, index, self) => self.indexOf(value) === index); // Unique emails

  if (recipients.length === 0) {
    return { message: 'Please provide at least one valid recipient email.' };
  }

  const emailContent = `Subject: ${validatedFields.data.subject}\n\n${validatedFields.data.body}`;

  const results: EmailResult[] = await Promise.all(
    recipients.map(async (recipient) => {
      const senderReputationScore = Math.random();
      const recipientEngagementScore = Math.random();

      try {
        const rateLimitResult: RateLimitEmailOutput = await rateLimitEmail({
          recipientEmail: recipient,
          emailContent,
          senderReputationScore,
          recipientEngagementScore,
        });

        if (rateLimitResult.shouldSendEmail) {
          // Here you would integrate with a real email service like Nodemailer
          return { recipient, status: 'Sent', reason: rateLimitResult.reason };
        } else {
          return { recipient, status: 'Blocked', reason: rateLimitResult.reason };
        }
      } catch (error) {
        console.error('Error with AI rate limiter for recipient:', recipient, error);
        return {
          recipient,
          status: 'Failed',
          reason: 'An error occurred while processing with the AI rate limiter.',
        };
      }
    })
  );

  const batch: EmailBatch = {
    id: new Date().toISOString(),
    subject: validatedFields.data.subject,
    body: validatedFields.data.body,
    recipients: recipients,
    results,
    sentAt: new Date().toISOString(),
  };

  return { message: 'Email batch processing complete.', batch };
}
